<template>
    <div class="container-fluid" id="app">
        <div class="row">
            <div class="col-md-6 offset-3" id="phoneBook">
                <div class="row">
                    <h2>Ki�iler</h2>
                </div>

                <div class="row" id="search">
                    <div class="col-md-11">
                        <input type="text" class="form-control" placeholder="Ara" />
                    </div>
                    <div class="col-md-1">
                        <button class="form-control">
                            <i class="fa fa-search"></i>
                        </button>
                    </div>
                </div>

                <div class="row" id="add">
                    <button type="button" class="btn btn-warning" id="btnAdd" data-toggle="modal" data-target="#addModal">
                        Yeni Kay�t
                    </button>
                </div>

                <ul>
                    <BookRow  @CallPerson="newPerson = $event" :person="person" v-for="person in bookList" :key="person.id"></BookRow>
                </ul>


            </div>
            <PhoneAdd @AddToBook="AddBook($event)"></PhoneAdd>
            <PhoneCall :call="newPerson"></PhoneCall>
            <PhoneEdit></PhoneEdit>
        </div>
    </div>
</template>

<script>
    import BookRow from "../src/PhoneBookRow"
    import PhoneAdd from "../src/PhoneAdd"
    import PhoneCall from "../src/PhoneCall"
    import PhoneEdit from "../src/PhoneEdit"
    import axios from "axios"
import { ServerResponse } from "http";
    export default {
        props:["message"],
        components: {
            BookRow,
            PhoneAdd,
            PhoneCall,
            PhoneEdit
        },
        data() {
            return {
                newPerson: {
                    id: 0,
                    Title: "",
                    Phone: "",
                    IsFavorite:true
                },
                bookList:
                    [
                        
                    ]
            }
        },
        methods: {
            AddBook($event) {

                this.newPerson.Title = $event.Title
                this.newPerson.Phone = $event.Phone

                axios.post("https://phonebook-92502.firebaseio.com/bookList.json", { Title: this.newPerson.Title, Phone: this.newPerson.Phone })
                    .then(response => {
                        console.log(response)
                        this.bookList.push(this.newPerson)
                    })
                    .catch(e => {
                        console.log(e)
                    })

                

            }
        },
        created() {
            axios.get("https://phonebook-92502.firebaseio.com/bookList.json")
                .then(response => {
                    console.log(response.data)
                    for (let key in response.data) {
                        this.bookList.push(response.data[key])
                    }
                })
                .catch(e => {
                    console.log(e)
                })
        },
        computed: {},
        watch: {}
    }
</script>

<style>
    body {
        background-image: url("../src/assets/bg.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-attachment: fixed;
    }

    #phoneBook {
        background-color: white;
        height: 100%;
        position: fixed;
    }

    ul {
        list-style-type: none;
        margin-top: 30px;
    }

    li {
        text-decoration: none;
        height: 60px;
        border-bottom: 1px solid black;
        padding-top: 15px;
    }

    #search {
        margin-top: 20px;
    }

    h2 {
        margin-left: 40%;
    }
    #btnAdd{
        margin-left:30%;
        margin-top:15px;
        width:400px;
    }

</style>